
import axios from 'axios'
import Mock from 'mockjs'
import mockData from '../mock/size'
import XHeader from 'vux/src/components/x-header/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Previewer from 'vux/src/components/previewer/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mapState, mapMutations} from 'vuex'

let oldSize='';
let oldSpecial='';
export default {
data() {
return{
tabIndex:0,
allowDate:'------',//
allowFlag:1,//
userSizeList:'',//
popup: false,//
currentList: '',//
selectSizeList:[],//
saveFlag:'1',//
currentTrIndex: '',
addressAllowDate:'-------',//
addressAllowFlag:1,//
selectProv:[],
selectCity:[],
selectCounty:[],
userProv:'',
userCity:'',
userCounty:'',
userDetail:'',
userTel:'',
userTelBack:'',
userLinkName:'',
buserProv:'',
buserCity:'',
buserCounty:'',
buserDetail:'',
buserTel:'',
buserTelBack:'',
buserLinkName:''
}
},
components: {
XHeader,
Card,
Tab,
TabItem,
Cell,
Group,
XTextarea,
CellBox,
GroupTitle,
Sticky,
TransferDom,
Previewer,
XTable,
Selector,
Badge,
LoadMore,
XButton,
XInput,
ButtonTab,
ButtonTabItem,
Popup,
XNumber,
Toast
},
created() {
var obj = this;

axios.get('../phone/userSize/queryAllData.json',{
params: {
userCode: obj.userCode
},timeout:20000
}).then(function (data) {
obj.allowDate = data.data.allowDate;//
obj.allowFlag = data.data.allowFlag;//
obj.addressAllowDate=data.data.addressAllowDate;//
obj.addressAllowFlag=data.data.addressAllowFlag;//
}).catch((err) => {
obj.$vux.toast.show({
type: 'warn',
text: ''
})
});
Mock.mock('../phone/userSize/queryAllData.json?userCode='+ this.userCode, mockData.queryAllData)


axios.get('../phone/userSize/loadArea.json',{
params: {
code:'',
level:'0'
},timeout:20000
}).then(function (data) {
obj.selectProv=data.data;
}).catch((err) => {
obj.$vux.toast.show({
type: 'warn',
text: ''
})
});
Mock.mock('../phone/userSize/loadArea.json?code=&level=0', mockData.loadArea.selectProv)

axios.get('../phone/userSize/queryuserSize.json',{
params: {
userCode: obj.userCode
},timeout:20000
}).then(function (data) {
obj.userSizeList = data.data;
}).catch((err) => {
obj.$vux.toast.show({
type: 'warn',
text: ''
})
});
Mock.mock('../phone/userSize/queryuserSize.json?userCode='+ this.userCode, mockData.queryuserSize)

axios.get('../phone/userSize/loadUserAddress.json',{
params:{
userCode: obj.userCode
},timeout:20000
}).then(function (data) {
obj.userProv=data.data.infoLinkProv;
obj.userCity=data.data.infoLinkCity;
obj.userCounty=data.data.infoLinkCounty;
obj.userDetail=data.data.infoLinkDetail;
obj.userTelBack=data.data.infoLinkTelBack;
if(data.data.infoLinkName!=''){
obj.userLinkName=data.data.infoLinkName;
obj.userTel=data.data.infoLinkTel;
}

obj.buserProv=data.data.infoLinkProv;
obj.buserCity=data.data.infoLinkCity;
obj.buserCounty=data.data.infoLinkCounty;
obj.buserDetail=data.data.infoLinkDetail;
obj.buserTelBack=data.data.infoLinkTelBack;
if(data.data.infoLinkName!=''){
obj.buserLinkName=data.data.infoLinkName;
obj.buserTel=data.data.infoLinkTel;
}
}).catch((err) => {
obj.$vux.toast.show({
type: 'warn',
text: ''
})
});
Mock.mock('../phone/userSize/loadUserAddress.json?userCode='+ this.userCode, mockData.loadUserAddress)

},
mounted() {

},
computed: {
...mapState({
userCode: 'userCode',//
pageScrollTop: 'pageScrollTop'
})
},
activated() {
},
directives: {
TransferDom
},
methods: {

optionalEdit(list, index) {
this.currentTrIndex = index
this.saveFlag='1';
var obj=this;
if(this.allowFlag=='1'&&list.CUSTOM_FLAG==''){

obj.$vux.loading.show({
text: 'Loading'
});
axios.get('../phone/userSize/queryMatCode.json',{
params: {
matId: list.STD_MAT_ID
},timeout: 20000
}).then(function (data) {
obj.$vux.loading.hide();
obj.selectSizeList = data.data;
}).catch((err) => {
obj.$vux.loading.hide();
obj.$vux.toast.show({
type: 'warn',
text: ''
});
});
Mock.mock('../phone/userSize/queryMatCode.json?matId='+ this.STD_MAT_ID, mockData.queryMatCode)

this.popup = true
oldSize=list.NATIONAL_SIZE;
oldSpecial=list.SPECIAL_REQ;
this.currentList = list
}
},

cancelOptionalEdit() {
this.popup = false;
this.currentList.NATIONAL_SIZE=oldSize;
this.currentList.SPECIAL_REQ=oldSpecial;
},
cancelOptionalForView() {
if(this.saveFlag=='1'){
this.popup = false;
this.currentList.NATIONAL_SIZE=oldSize;
this.currentList.SPECIAL_REQ=oldSpecial;
}
},

submitOptionalEdit() {
var obj=this;
if(!obj.currentList.NATIONAL_SIZE){
obj.$vux.toast.show({
type: 'warn',
text: ''
})
return false;
}

this.$vux.loading.show({
text: 'Loading'
});
var form = new FormData();
form.append('userCode',obj.currentList.userCode);
form.append('matId',obj.currentList.STD_MAT_ID);
form.append('oldRecord',obj.currentList.SIZE_ID);
form.append('size',obj.currentList.NATIONAL_SIZE);
form.append('measureSize',obj.currentList.MEASURE_SIZE);
form.append('special',obj.currentList.SPECIAL_REQ);
axios.post('../phone/userSize/saveModifySize.json',
form,{timeout:30000}
).then(function (data) {
obj.$vux.loading.hide();
if(data.data.flag=='1'){
obj.saveFlag='2';//
obj.$vux.toast.show({
type: 'success',
text: ''
});
obj.popup = false;
}else{
obj.popup = true;
obj.$vux.toast.show({
type: 'warn',
text: ''
});
}
}).catch((err) => {
obj.$vux.loading.hide();
obj.$vux.toast.show({
type: 'warn',
text: ''
});
});
},
changeProv(){//
var obj=this;
obj.selectCity=[];
obj.selectCounty=[];
axios.get('../phone/userSize/loadArea.json',{
params: {
code:obj.userProv,
level:'1'
},timeout:20000
}).then(function (data) {
obj.selectCity=data.data;
}).catch((err) => {
obj.$vux.toast.show({
type: 'warn',
text: ''
})
});
Mock.mock('../phone/userSize/loadArea.json?code='+ this.userProv + '&level=1', mockData.loadArea.selectCity)
},
changeCity(){//
var obj=this;
obj.selectCounty=[];
axios.get('../phone/userSize/loadArea.json',{
params: {
code:obj.userCity,
level:'2'
},timeout:20000
}).then(function (data) {
obj.selectCounty=data.data;
}).catch((err) => {
obj.$vux.toast.show({
type: 'warn',
text: ''
})
});
Mock.mock('../phone/userSize/loadArea.json?code='+ obj.userCity + '&level=2', mockData.loadArea.selectCounty)

},
closeadd(){//
var obj=this;
obj.userProv=obj.buserProv;
obj.userCity=obj.buserCity;
obj.userCounty=obj.buserCounty;
obj.userDetail=obj.buserDetail;
obj.userTelBack=obj.buserTelBack;
obj.userLinkName=obj.buserLinkName;
obj.userTel=obj.buserTel;
},
submitapply(){
var obj=this;
if(obj.userProv==''){
obj.$vux.toast.show({
type: 'warn',
text: ''
});
return false;
}
if(obj.userCity==''){
obj.$vux.toast.show({
type: 'warn',
text: ''
});
return false;
}
if(obj.userCounty==''){
obj.$vux.toast.show({
type: 'warn',
text: ''
});
return false;
}
var cityYes=false;
for(var i=0;i<obj.selectCity.length;i++){
if(obj.userCity==obj.selectCity[i]){
cityYes=true;
break;
}
}
if(!cityYes){
obj.$vux.toast.show({
type: 'warn',
text: ''
});
return false;
}
var countyYes=false;
for(var i=0;i<obj.selectCounty.length;i++){
if(obj.userCounty==obj.selectCounty[i]){
countyYes=true;
break;
}
}
if(!countyYes){
console.log('456');
obj.$vux.toast.show({
type: 'warn',
text: ''
});
return false;
}
this.$vux.confirm.show({
content:'',
onConfirm () {
obj.$vux.loading.show({
text: 'Loading'
});
var form = new FormData();
form.append('userCode',obj.userCode);
form.append('linkName',obj.userLinkName);
form.append('linkTel',obj.userTel);
form.append('linkTelBack',obj.userTelBack);
form.append('prov',obj.userProv);
form.append('city',obj.userCity);
form.append('county',obj.userCounty);
form.append('detail',obj.userDetail);
axios.post('../phone/userSize/saveUserAddress.json',
form,{timeout:30000}
).then(function (data) {
obj.$vux.loading.hide();
if(data.data.flag=='1'){
obj.saveFlag='2';//
obj.$vux.toast.show({
type: 'success',
text: ''
});
obj.buserProv=obj.userProv;
obj.buserCity=obj.userCity;
obj.buserCounty=obj.userCounty;
obj.buserDetail=obj.userDetail;
obj.buserTelBack=obj.userTelBack;
obj.buserLinkName=obj.userLinkName;
obj.buserTel=obj.userTel;
}else{
obj.popup = true;
obj.$vux.toast.show({
type: 'warn',
text: ''
});
}
}).catch((err) => {
obj.$vux.loading.hide();
obj.$vux.toast.show({
type: 'warn',
text: ''
});
});
}
});
}
}
}



